import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Cis10 from './components/cis10.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/cis10',
      name: 'cis10',
      component: Cis10,
      
    }
  ]
})
